
/**
 * @file 我的预约单
 */
@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';

.my_reservation_container {
  padding: 0 pr2(20);
  .tab_wrapper {
    width: 100%;
    height: pr2(44);
    margin-bottom: pr2(12);
    display: flex;
    align-items: center;
    li {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      font-size: pr2(16);
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: rgba(102,109,127,1);
      line-height: 1;
      margin-right: pr2(20);
      box-sizing: border-box;
      &:last-of-type {
        margin-right: 0;
      }
      &.active {
        font-size: pr2(22);
        font-family: PingFangSC-Medium;
        font-weight: 700;
        color: rgba(17,30,54,1);
        position: relative;
        line-height: 1;
        &:after {
          content: '';
          display: block;
          width: pr2(6);
          height: pr2(4);
          border-radius: pr2(5);
          background: rgba(0, 85, 255, 1);
          margin: pr2(3) auto 0 auto;
        }
      }
    }
  }
  .reservation_list_wrapper {
    width: 100%;
    >li {
      padding: pr2(16) pr2(20);
      box-sizing: border-box;
      min-height: pr2(146);
      box-shadow:0px 4px 20px 0px rgba(17,30,54,0.06);
      border-radius:4px;
      margin-bottom: pr2(12);
      .list_hd {
        width: 100%;
        display: flex;
        align-items: center;
        position: relative;
        margin-bottom: pr2(12);
        .dealer_type_lavel {
          width: pr2(31);
          height: pr2(17);
          text-align: center;
          line-height: pr2(17);
          box-sizing: border-box;
          background: rgba(0, 85, 255, 1);
          color: #fff;
          font-size: pr2(12);
          font-family: PingFangSC-Regular;
          border-radius: pr2(4) 0 pr2(4) 0;
          margin-right: pr2(5);
        }
        .dealer_name {
          max-width: pr2(176);
          font-size: pr2(16);
          font-family: PingFangSC-Medium;
          font-weight: 700;
          color: rgba(17,30,54,1);
          line-height: 1;
          margin-right: pr2(8);
          @include ellipsis();
        }
        .arrow_icon {
          width: pr2(12);
          height: pr2(12);
          background: url('data:image/png;base64,') no-repeat;
          background-size: 100% 100%;
        }
        .reservation_state {
          font-size: pr2(14);
          font-family: PingFangSC-Regular;
          font-weight: 400;
          color: rgba(255,102,0,1);
          line-height: 1;
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
          &.finished_state {
            color: #0055FF;
          }
          &.cancel_state {
            color: #666D7F;
          }
        }
      }
      .list_bd {
        display: flex;
        .dealer_img_wrapper {
          width: pr2(110);
          height: pr2(84);
          position: relative;
          margin-right: pr2(12);
          img {
            display: block;
            width: pr2(110);
            height: pr2(84);
            border-radius: pr2(4);
            @extend %athm-holdimg-white;
          }
          .blue_label {
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            width: pr2(58);
            height: pr2(18);
            background: rgba(0, 85, 255, 1);
            border-radius: pr2(4) 0 0 0;
          }
          .transparency_label {
            width: pr2(60);
            height: pr2(18);
            text-align: center;
            line-height: pr2(18);
            background: rgba(255, 102, 0, 1);
            font-size: pr2(12);
            font-family: PingFangSC-Semibold;
            font-weight: 600;
            color: rgba(255,255,255,1);
            position: absolute;
            top: pr2(3);
            left: pr2(3);
            border-radius: 0 pr2(4) 0 pr2(4);
          }
        }
        .list_bd_right {
          display: flex;
          flex: 1;
          flex-direction: column;
          justify-content: center;
          .appointment_time {
            font-size: pr2(12);
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: rgba(102,109,127,1);
            p {
              font-size:  pr2(12);
              font-family: PingFangSC-Regular;
              font-weight: 400;
              color:rgba(102,109,127,1);
              line-height: pr2(16);
            }
          }
          .btn_wrapper {
            display: flex;
            justify-content: flex-end;
            margin-top: pr2(12);
            padding-right: pr2(10);
            z-index: 999;
            .process_btn, .service_btn {
              width: pr2(76);
              height: pr2(32);
              border-radius: pr2(4);
              position: relative;
              display: flex;
              align-items: center;
              justify-content: center;
              font-size: pr2(14);
              font-family: PingFangSC-Regular;
              font-weight: 400;
              color: rgba(0,85,255,1);
              &:after {
                content: '';
                position: absolute;
                width: 200%;
                height: 200%;
                top: 0;
                left: 0;
                border: 1px solid rgba(0,85,255,0.2);;
                transform: scale(0.5);
                transform-origin: 0 0;
                box-sizing: border-box;
                border-radius: 4px;
              }
            }
            .process_btn {
              margin-right: pr2(12);
            }
          }
        }
      }
      .list_ft {
        width: 100%;
        height: pr2(36);
        background: rgba(248, 249, 252, 1);
        border-radius: pr2(4);
        display: flex;
        align-items: center;
        padding: 0 pr2(12);
        margin-top: pr2(12);
        box-sizing: border-box;
        position: relative;
        .green_point {
          width: pr2(5);
          height: pr2(5);
          border-radius: 50%;
          background: rgba(0, 204, 136, 1);
          margin-right: pr2(8);
        }
        .context {
          font-size: pr2(12);
          font-family: PingFangSC-Regular;
          font-weight: 400;
          color: rgba(102,109,127,1);
        }
        .arrow_icon {
          width: pr2(8);
          height: pr2(8);
          background: url('data:image/png;base64,') no-repeat;
          background-size: 100% 100%;
          position: absolute;
          top: 50%;
          right: pr2(12);
          transform: translateY(-50%);
        }
      }
    }
  }
}